"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Loader2 } from "lucide-react";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface ReportData {
  timestamp: string;
  summary: {
    totalProjects: number;
    totalBudget: string;
    averageProgress: number;
    riskDistribution: {
      low: number;
      medium: number;
      high: number;
    };
    statusDistribution: {
      inProgress: number;
      completed: number;
      onHold: number;
      notStarted: number;
    };
  };
  projects: Array<{
    projectId: string;
    projectName: string;
    department: string;
    description: string;
    metrics: {
      budget: string;
      progress: number;
      startDate: string;
      endDate: string;
      riskLevel: string;
      status: string;
    };
    resources: {
      humanResources: number;
      equipment: string[];
      contractors: string[];
    };
    risks: Array<{
      type: string;
      impact: string;
      mitigation: string;
    }>;
    recommendations: string[];
    details: string[];
  }>;
}

export default function Home() {
  const [file, setFile] = useState<File | null>(null);
  const [manualData, setManualData] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [reportData, setReportData] = useState<ReportData | null>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      setFile(e.target.files[0]);
      setError(null);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);
    setReportData(null);

    try {
      const formData = new FormData();
      if (file) {
        formData.append("file", file);
      }
      if (manualData) {
        formData.append("data", manualData);
      }

      const response = await fetch("/api/report/generate", {
        method: "POST",
        body: formData,
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Có lỗi xảy ra khi xử lý báo cáo.");
      }

      setReportData(data);
    } catch (err: any) {
      setError(err.message || "Có lỗi xảy ra khi xử lý báo cáo.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-50 to-gray-100">
      <div className="container mx-auto px-4 py-12">
        <Card className="p-8 bg-white shadow-xl rounded-xl max-w-4xl mx-auto">
          <h1 className="text-4xl font-bold text-gray-900 mb-6 text-center">
            Hệ thống Báo cáo Tự động - Tập đoàn PVN
          </h1>
          
          <div className="mb-10">
            <p className="text-gray-600 text-center text-lg">
              Hệ thống tự động xử lý và phân tích báo cáo dự án. Tải lên tài liệu hoặc nhập thông tin để bắt đầu.
            </p>
          </div>

          <form onSubmit={handleSubmit} className="space-y-8">
            <div className="space-y-3">
              <label className="block text-base font-medium text-gray-700">
                Tải lên tài liệu
              </label>
              <div className="mt-1 relative">
                <Input
                  type="file"
                  accept=".pdf,.doc,.docx,image/*"
                  onChange={handleFileChange}
                  className="w-full border-2 border-dashed border-gray-300 rounded-lg px-4 py-8 hover:border-gray-400 transition-colors cursor-pointer bg-gray-50"
                />
                <div className="absolute inset-0 pointer-events-none flex items-center justify-center">
                  <div className="text-center">
                    <p className="text-sm text-gray-500">
                      Kéo thả tập tin vào đây hoặc click để chọn
                    </p>
                    <p className="text-xs text-gray-400 mt-1">
                      PDF, DOC, DOCX hoặc hình ảnh (Tối đa 500MB)
                    </p>
                  </div>
                </div>
              </div>
            </div>

            <div className="space-y-3">
              <label className="block text-base font-medium text-gray-700">
                Hoặc nhập thông tin thống kê
              </label>
              <Textarea
                value={manualData}
                onChange={(e) => setManualData(e.target.value)}
                placeholder="Nhập thông tin thống kê của dự án..."
                className="min-h-[200px] resize-none focus:ring-2 focus:ring-black focus:border-transparent"
              />
            </div>

            {error && (
              <Alert variant="destructive">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            <Button
              type="submit"
              className="w-full py-6 text-lg font-semibold transition-transform hover:scale-[1.02]"
              disabled={loading}
            >
              {loading ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Đang xử lý...
                </>
              ) : (
                "Tạo báo cáo"
              )}
            </Button>
          </form>

          {loading && (
            <div className="mt-8 text-center bg-gray-50 rounded-lg p-8">
              <Loader2 className="h-12 w-12 animate-spin mx-auto text-gray-700" />
              <p className="mt-4 text-gray-600 text-lg">Đang xử lý dữ liệu...</p>
              <p className="mt-2 text-gray-500 text-sm">Vui lòng đợi trong giây lát...</p>
            </div>
          )}

          {reportData && !loading && (
            <div className="mt-12 space-y-8">
              <div className="bg-gray-50 p-6 rounded-lg">
                <h2 className="text-2xl font-semibold mb-6 text-center">Tổng quan báo cáo</h2>
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                  <div className="bg-white p-4 rounded-lg shadow">
                    <h3 className="text-sm font-medium text-gray-500">Tổng số dự án</h3>
                    <p className="text-2xl font-bold">{reportData.summary.totalProjects}</p>
                  </div>
                  <div className="bg-white p-4 rounded-lg shadow">
                    <h3 className="text-sm font-medium text-gray-500">Tổng ngân sách</h3>
                    <p className="text-2xl font-bold">{reportData.summary.totalBudget}</p>
                  </div>
                  <div className="bg-white p-4 rounded-lg shadow">
                    <h3 className="text-sm font-medium text-gray-500">Tiến độ trung bình</h3>
                    <p className="text-2xl font-bold">{reportData.summary.averageProgress.toFixed(1)}%</p>
                  </div>
                  <div className="bg-white p-4 rounded-lg shadow">
                    <h3 className="text-sm font-medium text-gray-500">Thời gian báo cáo</h3>
                    <p className="text-lg font-medium">{reportData.timestamp}</p>
                  </div>
                </div>
              </div>

              <div>
                <h2 className="text-2xl font-semibold mb-6">Chi tiết dự án</h2>
                <div className="space-y-6">
                  {reportData.projects.map((project, index) => (
                    <Card key={index} className="p-6">
                      <div className="space-y-4">
                        <div className="flex justify-between items-start">
                          <div>
                            <h3 className="text-xl font-semibold">{project.projectName}</h3>
                            <p className="text-gray-500">{project.projectId} - {project.department}</p>
                          </div>
                          <div className="text-right">
                            <div className="text-lg font-semibold">{project.metrics.budget}</div>
                            <div className="text-sm text-gray-500">Tiến độ: {project.metrics.progress}%</div>
                          </div>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                          <div>
                            <h4 className="font-medium mb-2">Thông tin chung</h4>
                            <div className="space-y-1 text-sm">
                              <p>Bắt đầu: {project.metrics.startDate}</p>
                              <p>Kết thúc dự kiến: {project.metrics.endDate}</p>
                              <p>Trạng thái: {project.metrics.status}</p>
                              <p>Mức độ rủi ro: {project.metrics.riskLevel}</p>
                            </div>
                          </div>

                          <div>
                            <h4 className="font-medium mb-2">Nguồn lực</h4>
                            <div className="space-y-1 text-sm">
                              <p>Nhân sự: {project.resources.humanResources} người</p>
                              <p>Thiết bị: {project.resources.equipment.join(", ")}</p>
                              <p>Nhà thầu: {project.resources.contractors.join(", ")}</p>
                            </div>
                          </div>
                        </div>

                        <div>
                          <h4 className="font-medium mb-2">Rủi ro và Giải pháp</h4>
                          <div className="space-y-2">
                            {project.risks.map((risk, i) => (
                              <div key={i} className="bg-gray-50 p-3 rounded">
                                <p className="font-medium">{risk.type}</p>
                                <p className="text-sm text-gray-600">Tác động: {risk.impact}</p>
                                <p className="text-sm text-gray-600">Giải pháp: {risk.mitigation}</p>
                              </div>
                            ))}
                          </div>
                        </div>

                        <div>
                          <h4 className="font-medium mb-2">Khuyến nghị</h4>
                          <ul className="list-disc pl-5 space-y-1">
                            {project.recommendations.map((rec, i) => (
                              <li key={i} className="text-sm text-gray-600">{rec}</li>
                            ))}
                          </ul>
                        </div>
                      </div>
                    </Card>
                  ))}
                </div>
              </div>
            </div>
          )}
        </Card>
      </div>
    </main>
  );
}
